.app{
   height: 100vh;
   display: flex;
   background-color: #020230;
   color: white;
}
.main{
   width: 75%;
   background: linear-gradient(to bottom,rgba(0,0,0,0),#020230),
    url('./additionals/bg.jpg') center;
    display: flex;
    flex-direction: column;
}
.top{
   height: 50%;
   position: relative;
}
.bottom{
   height: 50%;
}
.timer{
   height: 70px;
   width: 70px;
   border-radius: 50%;
   border: 4px solid white;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 30px;
   font-weight: 700;
   position: absolute;
   bottom: 10px;
   left: 60px;
}
.trivia{
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   align-items: center;
}
.question{
   width: 80%;
   background: linear-gradient(#100241,black);
   border: 2px solid white;
   padding: 20px;
   text-align: center;
   border-radius: 10px;
   font-size: 20px;
}
.answers{
   width: 100%;
   display: flex;
   justify-content: center;
   flex-wrap: wrap;

}
.answer{
   width: 40%;
   padding: 10px;
   text-align: center;
   background: linear-gradient(#0e0124,#22074d);
   border: 1px solid white;
   border-radius: 15px;
   font-size: 20px;
   margin: 0px 10px 20px 10px;
   cursor: pointer;

}
.answer:hover,
.answer.active{
   background: mediumblue;
}
.answer.correct{
   animation: correct 3s ease forwards;
}
@keyframes correct {
   0%,22%,40%{
      background: blue;
   }
   20%,42%,60%{
      background: linear-gradient(#0e0124,#22074d);
   }
   65%,100%{
      background: green;
   }
}
.answer.wrong{
   animation: wrong 3s ease forwards;
}
@keyframes wrong {
   0%,22%,40%{
      background: blue;
   }
   20%,42%,60%{
      background: linear-gradient(#0e0124,#22074d);
   }
   65%,100%{
      background: crimson;
   }
}
.endText{
   position: relative;
   top:0;
   bottom:0;
   right:0;
   left:0;
   margin: auto;
   font-size: 60px;
   color: yellow;

}
.pyramid{
   width: 25%;
   display: flex;
   align-items: center;
   justify-content: center;
}
.moneyList{
   list-style: none;
   width: 100%;
   padding: 20px;
}
.moneyListItem{
display: flex;
align-items: center;
padding: 5px;
border-radius: 5px;
}
.moneyListItem.active{
   background-color: teal;
}
.moneyListItemNumber{
   width: 30%;
font-size: 18px;
font-weight: 100;
}
.moneyListItemAmount{
font-size: 20px;
font-weight: 300;

}